.app__laurels_awards {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 3rem;
}

.app__laurels_awards-card {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    min-width: 230px;
    margin: 1rem;
}

.app__laurels_awards-card img {
    width: 70px;
    height: 70px;
}

.app__laurels_awards-card_content {
    display: flex;
    flex-direction: column;
    margin-left: 1rem;
}

.app__laurels_awards-card_content p:last-child {
    font-size: 15px;
    line-height: 175%;
}

#Awards .app__wrapper_img {
    flex: 0.8;
}

@media (min-width: 2000px) {
    .app__laurels_awards-card {
        min-width: 390px;
    }
}

@media (max-width: 450px) {
    .app__laurels_awards-card {
        min-width: 100%;
        margin: 1rem 0;
    }    
}